import Vue from 'vue';
import List from './List.js';
import './style.css';

const CustomSelect = Vue.component('Item', {
  name: 'custom-select',
  props: {
    open: {
      type: Boolean,
      default: false
    },
  },
  provide(){
      return {
          setOpennessList: (val)=>{
              this.opennessList = val;
              console.log(this);
          }
      }
  },
  data () {
    return {
      items: [
          {
              name: '–ú–∞—à–∏–Ω—ã',
              items: [
                  {
                      name: '–õ–µ–≥–∫–æ–≤—ã–µ'
                  },
                  {
                      name: '–ì—Ä—É–∑–æ–≤—ã–µ'
                  }
              ]
          },
          {
              name: '–ò–≥—Ä—É—à–∫–∏',
              items: [
                  {
                      name: '–†–æ–±–æ—Ç—ã'
                  },
                  {
                      name: '–ö—É–∫–ª—ã'
                  }
              ]
          },
          {
              name: '–û–¥–µ–∂–¥–∞',
              items: [
                  {
                      name: '–ú—É–∂—Å–∫–∞—è'
                  },
                  {
                      name: '–ñ–µ–Ω—Å–∫–∞—è'
                  }
              ]
          }
      ],
      opennessList: false,
      value: '–í—Å–µ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏'
    }
  },
  created(){
  },
  methods: {
      open(){
           console.log(this.opennessList);
           this.opennessList = true;
      }
  },
  render: function (h) {
    return (
        <div class="custom-select">
            <div onClick={this.open} class="custom-select-title">{ this.value }</div>
            { this.opennessList ? <List items={this.items} onClose={()=>{this.opennessList = false}}  /> : '' }
        </div>
    )
  }
})

export default CustomSelect;


