<template>
	<div class="month">
        <div class="month__header" @click="showActList">
          <div class="month__name">{{ getNameMonth() }}</div>
          <div class="month__finance_wrapepr">
            <div v-if="finance['‚ÇΩ']" class="month__finance">{{ finance['‚ÇΩ'] }} ‚ÇΩ <span> / </span></div>
            <div v-if="finance['$']"  class="month__finance">{{ finance['$'] }} $<span> / </span></div>
            <div v-if="finance['‚Ç¨']"  class="month__finance">{{ finance['‚Ç¨'] }} ‚Ç¨<span> / </span></div>
          </div>
        </div>
        <div class="mini-list-product" v-if="showList">
          <miniProduct
            v-for="product in list"
            :name="product.name"
            :price="product.price"
            :img="product.img"
            :currency="product.currency"
	      ></miniProduct>
        </div>
      </div>
</template>



<script>
import miniProduct from './mini-product';
export default {
  components:{
      miniProduct
  },
  name: 'finance',
  props: {
    month: {
      type: Number,
      default: '',
    },
    finance: {
      type: Object,
      default: '',
    },
    list: {
      type: Array,
      default: false,
    },
    price: {
      type: Number,
      default: 0,
    }
  },
  data () {
    return {
        showList: false
    }
  },
  created(){
  },
  computed: {
  },
  methods: {
      showActList(){
          if(this.showList){
              this.showList = false;
          }else{
              this.showList = true;
          }
      },
      getNameMonth(){
        let array = {
          1: '–Ø–Ω–≤–∞—Ä—å',
          2: '–§–µ–≤—Ä–∞–ª—å',
          3: '–ú–∞—Ä—Ç',
          4: '–ê–ø—Ä–µ–ª—å',
          5: '–ú–∞–π',
          6: '–ò—é–Ω—å',
          7: '–ò—é–ª—å',
          8: '–ê–≤–≥—É—Å—Ç',
          9: '–°–µ–Ω—Ç—è–±—Ä—å',
          10: '–û–∫—Ç—è–±—Ä—å',
          11: '–ù–æ—è–±—Ä—å',
          12: '–î–µ–∫–∞–±—Ä—å',
        };
        console.log(this.month);
        return array[this.month];
      },
  }
}
</script>

<style>
.month__finance_wrapepr {
    display: flex;
    margin-top: 5px;
}
.month__header {
    border-radius: 2px;
    padding: 9px;
    background: #dbf7ff;
    border-bottom: 1px solid #00c4ff;
}
.month__name {
    display: inline-block;
    font-style: normal;
    color: #000000;
    color: #00c4ff;
}
.month__finance span {
    color: #ccc;
}
.month__finance {
    display: inline-block;
    font-size: 11px;
    margin-left: 5px;
}
.month__finance:last-child span{
  display: none;
}
</style>



